
import React, { Component } from 'react';
import Logo from './components/common/logo/Logo'

import io from 'socket.io-client';
import './App.scss';

import * as util from './data.service.js';

class App extends Component {
  state = {
    connectionBtnDisabled: false,
    createMode: true,
    mode: 'light',
    createCode: '',
    joinCode: '',
    connectedCode: '',

    connected: false,
    infoData: {},
    textToSend: '',
    fileToSend: null,
    response: [],
    loading: false,
    activeOldData: []

  }
  messageTimes = 0;
  tone = new Audio();
  // alerter
  setTimeoutAlerterGlobal = null
  isAlerterActiveGlobal = false
  iconHash = {
    'svg': 'fa fa-image mr-2',
    'jpg': 'fa fa-image mr-2',
    'jpeg': 'fa fa-image mr-2',
    'png': 'fa fa-image mr-2'
  }
  domain = '/' // 'http://localhost:8080/'
  socket;
  oldData = util.getData();
  activeOldDataIndex = 0;
  file;

  initSocket(init) {
    if (init) {
      this.socket = io(this.domain);
      return;

    } else {

      this.socket.destroy();
      delete this.socket;
      this.socket = null;


      this.initSocket(true);
      this.connect(this.state.connectedCode, true)


    }

  }

  incActiveOldData = () => {
    let activeOldData = [...this.oldData];

    activeOldData = activeOldData.splice(0, this.activeOldDataIndex + 1)
    this.setState({ activeOldData })

    this.activeOldDataIndex = this.activeOldDataIndex + 1
  }
  alerter = (type, msg, icon, timer) => {
    let lTimer = 4000;
    if (timer) {
      lTimer = timer;
    }
    if (!type) {
      throw console.error('No type in alert');

    }
    const alerterDiv = document.getElementById('alerter');
    if (!alerterDiv) {
      return;
    }
    if (!this.isAlerterActiveGlobal) {
      alerterDiv.innerHTML = `
                        <div class="alert alert-${type} animated fadeInDown squared">
                        <i class="fa ${icon} mr-2"></i>
                                ${msg === '' ? "<i class='fa fa-check-circle'>  </i>" : msg}
                        </div>
                        `;
      alerterDiv.style.display = 'inline-block';
      this.isAlerterActiveGlobal = true;

      this.setTimeoutAlerterGlobal = setTimeout(() => {
        this.isAlerterActiveGlobal = false;
        alerterDiv.style.display = 'none';
      }, lTimer)
    } else {
      this.isAlerterActiveGlobal = false;
      clearTimeout(this.setTimeoutAlerterGlobal)
      this.alerter(type, msg);

    }


  }
  // copy to clipboard

  preCopy = (text, alert) => {

    if (text) {
      util.copy(text)
      if (alert) {
        this.alerter('success', 'Copied  to clipboard <br>' + alert, 'fa-check', 10000);
        return;
      }
      this.alerter('success', 'Copied  to clipboard', 'fa-check');
      return;
    }
  }

  toggleMode = (e, force) => {
    if (force) {
      this.setState({ mode: force })
      return;
    }
    const mode = this.state.mode === 'light' ? 'dark' : 'light'
    localStorage.setItem('___color', mode)


    this.setState({ mode })


  }
  componentDidMount() {
    this.initSocket(true);
    const prevColor = localStorage.getItem('___color');

    if (prevColor) {
      this.toggleMode(null, prevColor);
    }
    // check for query params
    const urlParams = new URLSearchParams(window.location.search)

    if (urlParams.has('code')) {
      this.setState({ createMode: false, joinCode: urlParams.get('code') })


    }

    this.codeGenerator();


    this.incActiveOldData();
  }
  codeGenerator = () => {
    const length = 5;
    let result = '';
    const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    const charactersLength = characters.length;
    for (let i = 0; i < length; i++) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }


    this.setState({ createCode: result })

  }
  handleUpload = (e) => {
    this.file = e.target.files[0];
    if (!this.file) {
      return
    }


    const fileReader = new FileReader();

    fileReader.readAsArrayBuffer(this.file);
    fileReader.onload = (evt) => {
      const arrayBuffer = fileReader.result;

      console.log(typeof arrayBuffer)

      const fileToSend = {
        name: this.file.name,
        size: this.file.size,
        type: this.file.type,
        data: arrayBuffer



      }


      this.setState({ fileToSend });


    }






  }
  inputUpdate = (e) => {
    this.setState({ [e.target.name]: e.target.value })
  }
  connect(code, isJoin) {
    this.socket.on('disconnect', () => {
      if (this.state.connectedCode) {

        this.alerter('info', '..Reconnecting to ' + this.state.connectedCode + ' ðŸ‘€ðŸ‘€', 'fa-info', 10000)


        this.initSocket();


      } else {
        window.location.reload()
      }
    });
    this.setState({ connectionBtnDisabled: true })



    this.socket.emit('create', { code, isJoin })


    this.socket.on('sync', (_connectedCode) => {

      const connectedCode = _connectedCode;
      this.setState({ connected: true, connectedCode, loading: false });
      this.alerter('success', 'You are Connected âœ”âœ”', 'fa-check')


      this.onMessageHandler();



      this.socket.on('upload error', (data) => {
        if (data) {
          alert('error');
        }
      })




    })
    this.socket.on('info', (data) => {
      if (data) {

        const previousLen = this.state.infoData.length || 1;
        const currentLen = data.length;

        if (currentLen > previousLen) {
          // a device connected
          this.alerter('info', 'A device has Connected ðŸŽ‰ðŸŽ‰', 'fa-info')
        }
        if (currentLen < previousLen) {
          // a device disconnected

          this.alerter('info', 'A device has disconnected ðŸ¤·â€â™‚ï¸ðŸ¤·â€â™‚ï¸', 'fa-info')

        }


        this.setState({ infoData: data });
      }
    })


    this.socket.on('404', (data) => {
      if (data) {

        this.socket.removeAllListeners()

        this.alerter('danger', '..Connection not found, Please create a new connection or check your code again')

        this.setState({ connected: false, connectionBtnDisabled: false, connectedCode: null, });

      }
    })


  }
  onMessageHandler = () => {

    this.socket.on('message', (data) => {
      if (data) {

        this.tone.src = "/assets/audio/message.mp3";
        this.tone.play().then(r => r).catch(e => e)


        if (data.isFile) {
          this.setState({ loading: false });
        }

        this.setState({ response: [...this.state.response, data] });
        util.setData(this.state.connectedCode, this.state.response)
      }
    })
  }
  sender = () => {
    this.messageTimes++
    if(this.messageTimes === 1) {
      this.tone.play();
    }

    if (this.state.infoData.length < 2) {
      const proceed = window.confirm('This is the only device connected, are you sure you want to send this file')
      if (!proceed) {
        return
      }
    }
    let isFile;

    if (this.state.fileToSend) {
      isFile = true;
      this.setState({ loading: true });
    }

    const data = { file: this.state.fileToSend, text: this.state.textToSend, code: this.state.connectedCode, isFile }
    this.socket.emit('message', data);


    this.setState({ textToSend: '', fileToSend: null })
  }
  clearLs = () => {
    util.clear();
    this.oldData = null;
    window.location.reload();
  }
  render() {


    return (
      <div className={this.state.mode}>
        {
          this.state.loading ?

            <div className="loading">
              <img alt="loader" src="/assets/loader.svg" />
            </div>
            : ''

        }
        <div className="container main-body">
          <div id="alerter" className="rounded">

          </div>
          <div className="navbar">
            <h3><i className="fab fa-cloudsmith text-primary mr-1"></i>   <Logo />




            </h3>
            <span onClick={this.toggleMode}>
              {this.state.connected ?
                <span className="fa fa-check-circle mr-2 text-success"></span>
                :
                <span className="fa fa-times-circle mr-2 text-danger"></span>
              }
              {this.state.mode === 'light' ? <i className="fa fa-moon"></i> : <i className="fa fa-sun text-white"></i>}


            </span>

          </div>
          Share files across your devices, no sign up no login. just share! Just like  AirDrop
<br></br>

          {/** sent or recieved files stays here */}




          <br></br>
          {/** share mode */}
          {!this.state.connected ?
            <div onClick={() => this.setState({ createMode: !this.state.createMode })} className="row">
              <div className="col-6 cursor">

                <div className={this.state.createMode ? 'bg-primary text-white rounded p-2 text-center ' : 'rounded p-2 text-center'}>
                  <h3>

                    Create</h3>


                </div>

              </div>
              <div className="col-6 cursor">

                <div className={!this.state.createMode ? 'bg-primary text-white rounded p-2 text-center ' : 'rounded p-2 text-center'}>

                  <h3>Join</h3>


                </div>

              </div>
            </div>
            :
            <div className="border mb-3 p-2 border-secondary rounded">
              <div className="text-right">

                <button onClick={() => window.location.reload()} className="btn btn-sm btn-danger"><i className="fa fa-times mr-2"></i>Exit connection</button>
              </div>
              <br></br>
              <div className="border rounded p-2">
                Connected to <code>{this.state.connectedCode}</code>
                <br></br>
                <div className="btn-group mb-1">
                  <button onClick={() => this.preCopy(this.state.connectedCode)} className="btn btn-sm btn-primary">
                    <i className="fa fa-copy mr-1"></i>
                    Copy</button>
                  <button onClick={() => this.preCopy(window.location.hostname + '?code=' + this.state.connectedCode, 'Share this link so other devices can automatically join')} className="btn btn-sm btn-outline-primary">
                    <i className="fa fa-share mr-1"></i>Share</button>
                </div>
                <br />
                Connected devices: {this.state.infoData.length}
                <br />
                {this.state.infoData.length < 2 ?
                  <small className="text-info">Please connect another device, Only you is recieving these files</small>
                  : ''
                }

              </div>
              <br></br>
              Files you share will appear here
              <br></br>
              {this.state.response.map((res, index) => {

                return <div key={index} className="border rounded p-2 mt-3">
                  {
                    !res.isFile ?
                      <div>
                        {res.text}
                        <br></br>
                        <small className="d-block text-muted text-right">
                          <button onClick={() => this.preCopy(res.text)} className="btn btn-sm btn-primary mr-2">
                            <i className="fa fa-copy mr-1"></i>
                            Copy</button>
                          <i className="far fa-sticky-note mr-1"></i>
                          Note
                          {res.date ? <span className="ml-1 date">{new Date(res.date).toLocaleString()}</span> : ''}
                        </small>
                      </div>
                      :
                      <div>
                        {res.file.name}
                        <br></br>
                        {res.text}

                        <small className="d-block text-muted text-right">

                          <a href={"/api/get/" + res.file.data} target='_blank' rel="noopener noreferrer">
                            <i className="fa fa-cloud-download-alt fa-big mr-1 text-secondary"></i>
                          </a>
                          <button onClick={() => this.preCopy(window.location.hostname + "/api/get/" + res.file.data)} className="btn btn-sm btn-primary mr-2">
                            <i className="fa fa-copy mr-1"></i>
                            Copy</button>



                          <i className={this.iconHash[res.file.ext]}> </i>
                          {res.file.ext}
                          {res.date ? <span className="ml-1 date">{new Date(res.date).toLocaleString()}</span> : ''}
                        </small>

                      </div>


                  }


                </div>

              })

              }
            </div>
          }

          <br></br>

          <div className="row ">
            <div className="col-md-6">
              {!this.state.connected ?
                <div>
                  {this.state.createMode ?
                    <div>
                      <small className="text-warning">Your create code</small>

                      <h1 ><code><b>{this.state.createCode}</b></code></h1>

                      <div className="btn-group mb-1">
                        <button onClick={() => this.preCopy(this.state.createCode)} className="btn btn-sm btn-primary">
                          <i className="fa fa-copy mr-1"></i>
                          Copy</button>
                        <button onClick={() => this.preCopy(window.location.hostname + '?code=' + this.state.createCode, 'Share this link so other devices can automatically join')} className="btn btn-sm btn-outline-primary">
                          <i className="fa fa-share mr-1"></i>Share</button>
                      </div>
                      <br></br>
                      <br></br>

                      <button onClick={() => this.connect(this.state.createCode)} className="btn btn-outline-primary btn-block" disabled={!this.state.connectionBtnDisabled ? false : true} type="button">Create Connection</button>


                    </div>
                    :
                    <div>
                      <small className="text-warning">Enter Your join code</small>
                      <br></br>

                      <input className="form-control" placeholder="Enter your join code " onChange={this.inputUpdate} name="joinCode" value={this.state.joinCode} />

                      <br></br>

                      <button onClick={() => this.connect(this.state.joinCode, true)} className="btn btn-outline-primary btn-block" disabled={this.state.joinCode === '' || this.state.connectionBtnDisabled ? true : false} type="button">Join Connection</button>

                    </div>



                  }
                </div>
                :

                ''}
              {/**
              when connected
              
              
              */
                this.state.connected ?
                  <div className="border rounded p-2">


                    Upload File
{this.state.fileToSend ?
                      <div>
                        <div className="text-right p-2">
                          <i onClick={() => this.setState({ fileToSend: null })} className="fa cursor fa-times text-danger"></i>
                        </div>
                        Name: {this.state.fileToSend.name},
<br></br>
                        Size: {this.state.fileToSend.size / 1024} kb,
</div>


                      : ''}

                    <br></br>
                    <div className="text-center">
                      <br></br>
                      <label htmlFor="fileinput" className="cursor d-block p-5"> <i className="fa fa-cloud-upload-alt fa-big"></i></label>
                      <input id="fileinput" type="file" onChange={this.handleUpload} className="form-control d-none" />

                      <small>Files are only available on our servers for 10 Munites, after which they will be deleted</small>
                      <br></br>
                      <small>File upload is in beta, please restrict file size to  2 mb</small>
                    </div>
                    <hr />

                    Send Text

                    <textarea onChange={this.inputUpdate} inputMode="text" name="textToSend" className="form-control " value={this.state.textToSend}></textarea>
                    <br />
                    <button onClick={this.sender} className="btn btn-primary btn-block">Send</button>

                  </div>
                  : ''
              }


            </div>
            <div className="col-md-6">
              <div className="">
                <span className="underline">How to use</span>
                <br></br>
                <ol>
                  <li> If you do not have an existing connection, Create a connection. If you have a connection, click join and enter the create code</li>
                  <li> When you care connected, you will be able to see the number of devices in your network </li>
                  <li> You can send / recieve texts or files instantly </li>
                  <li> To leave the connection, click Exit connection </li>
                </ol>
                <br />
                <span className="text-info">On Iphones, You might be restricted to downloading just media files, due to ios  limitations, </span>

                <br></br>

                <span className="underline">Tips</span>
                <br></br>
                <ul>
                  <li> You can always connect back to the same connection so far there is atleast one device active. Connection only deletes when all devices have left, in this case, you will have to create a new connection</li>

                </ul>

              </div>
            </div>

          </div>
          <hr className="text-secondary" />
          {
            this.state.activeOldData && this.state.activeOldData.length ?
              <div className="border border-secondary  rounded">
                <div className="bg-secondary p-3">
                  <div className="row ">
                    <div className="col-9 col-md-6">              <h3>
                      Old Data
              </h3>
                      <small>We use your local storage to keep track of your old data</small>
                    </div>
                    <div className="col-3 col-md-6 text-right">
                      <button onClick={() => this.clearLs()} className="btn btn-sm btn-info"><i className="fab fa-cloudscale mr-2"></i>Clear</button></div>

                  </div>
                </div>
                <br />


                <div className="p-3">
                  {this.state.activeOldData.map((code, index2) => {

                    return (
                      <div key={index2} className="mt-2">
                        <h4>{code.code}</h4>
                        <div className="border rounded p-2 mt-3">

                          {code.response.map((res, index3) => {

                            return (<div key={index3}>
                              {
                                !res.isFile ?
                                  <div>
                                    {res.text}
                                    <br></br>
                                    <small className="d-block text-muted text-right">
                                      <button onClick={() => this.preCopy(res.text)} className="btn btn-sm btn-primary mr-2">
                                        <i className="fa fa-copy mr-1"></i>
                                        Copy</button>
                                      <i className="far fa-sticky-note mr-1"></i>
                                      Note
                                       {res.date ? <span className="ml-1 date">{new Date(res.date).toLocaleString()}</span> : ''}
                                    </small>
                                  </div>
                                  :
                                  <div>
                                    {res.file.name}
                                    <br></br>
                                    {res.text}



                                    <small className="d-block text-muted text-right">

                                      {res.date && Number(new Date() - Number(new Date(res.date)) <= 600000) ?
                                        <span>
                                          <a href={"/api/get/" + res.file.data} target="_blank" rel="noopener noreferrer">
                                            <i className="fa fa-cloud-download-alt fa-big mr-1 text-secondary"></i>
                                          </a>
                                          <button onClick={() => this.preCopy(window.location.hostname + "/api/get/" + res.file.data)} className="btn btn-sm btn-primary mr-2">
                                            <i className="fa fa-copy mr-1"></i>
                                            Copy</button>
                                        </span>

                                        :
                                        'file expired'
                                      }

                                      <i className={this.iconHash[res.file.ext]}> </i>
                                      {res.file.ext}
                                      {res.date ? <span className="ml-1 date">{new Date(res.date).toLocaleString()}</span> : ''}
                                    </small>
                                    <hr />
                                  </div>


                              }
                            </div>)

                          })}


                        </div>
                      </div>)

                  })

                  }
                  <br />
                  {this.activeOldDataIndex < this.oldData.length ? <button onClick={this.incActiveOldData} className="btn btn-sm btn-primary">Load more</button> : ''}
                </div>






              </div>
              : ''

          }


          <br></br>
          <br></br>
          <br></br>
          <br></br>
          <br></br>




        </div>
        <footer className={this.state.mode === 'light' ? "text-center p-3 bg-white" : 'text-center p-3 bg-cus-dark'}>

          Made with <i className="fa fa-heart text-danger"></i> by <a href="http://ayotycoon.github.io/">Sunmola Ayokunle</a> | <a href="https://twitter.com/ayotycoon"><i className="fab fa-twitter mr-1 ml-1 "></i></a>  | <span >Help Ayokunle find a job! <i className="far fa-smile-wink"></i> | <a href="https://cvngayotycoon.herokuapp.com/" target="_blank" rel="noopener noreferrer">Generate your CV</a></span>
        </footer>




      </div>

    );
  }

}

export default App;
